<template>
  <div class="home" v-if="dataSource">
    <div class="swiper">
      <div
        class="swiperItem"
        v-for="(item, index) in v2sliderows"
        :key="index"
        @click="swiperClick(item)"
      >
        <img class="image" :src="item.pic" mode="widthFix" />
        <span v-text="item.title"></span>
      </div>
    </div>
    <list title="香蕉头条dayrows" :dataList="dataSource.dayrows"></list>
    <list title="最新视频latestrows" :dataList="dataSource.latestrows"></list>
    <list title="猜你喜欢likerows" :dataList="dataSource.likerows"></list>
    <list title="偷拍自拍a_vodrows" :dataList="dataSource.a_vodrows"></list>
    <list title="成人动漫b_vodrows" :dataList="dataSource.b_vodrows"></list>
    <list title="经典伦理c_vodrows" :dataList="dataSource.c_vodrows"></list>
    <list title="中文字幕d_vodrows" :dataList="dataSource.d_vodrows"></list>
    <list title="不雅视频tagvodrows" :dataList="dataSource.tagvodrows"></list>
    <list title="热片视频hotrows" :dataList="dataSource.hotrows"></list>
  </div>
</template>

<script>
import config from "../js/config.js";
import list from "../components/list.vue";
import { mapState, mapMutations, mapGetters, mapActions } from "vuex";
export default {
  name: "keepAlive",
  components: {
    list,
  },
  data() {
    return {
      dataSource2: {},
    };
  },
  computed: {
    ...mapState({
      dataSource: (state) => state.banana.home.dataSource,
    }),
    v2sliderows() {
      let v2sliderows = [];
      if (this.dataSource && this.dataSource.v2sliderows) {
        this.dataSource.v2sliderows.forEach((item) => {
          if (item.scene == "special.detail") {
            v2sliderows.push(item);
          }
        });
      }
      return v2sliderows;
    },
  },
  activated(){
    this.loadData();
  },
  created() {
    this.loadData();
  },
  methods: {
    swiperClick(item) {
      this.$router.push({
        path: "./detail?spid=" + item.spid,
      });
    },
    loadData() {
      console.log("config", config, config.query());
      this.$axios
        .request({
          url: config.home,
          params: config.query(),
        })
        .then((res) => {
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            this.$store.commit("dataSource", res.data.data);
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch((res) => {
          weui.topTips(res.message || "fail");
        });
    },
  },
};
</script>

<style lang="less" scoped>
.home {
  .swiper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .swiperItem {
      margin: 5px;
      position: relative;
      box-shadow: rgb(170, 170, 170) 0px 0px 14px 0px;
      .image {
        width: 320px;
        display: block;
      }
    }
  }
}
</style>
