<template>
  <div class="list">
    <div class="header" @click="headerClick" v-if="title">
      <span class="title">{{ title }}</span>
      <span class="more">更多</span>
    </div>
    <div class="numInfo">{{ list.length }}/{{ dataList.length }}</div>
    <div class="items">
      <div
        class="item"
        :class="{ itemMin: isMin }"
        v-for="(item, index) in list"
        :key="index + item.vodid"
        @click="itemClick(item, index)"
      >
        <video
          class="video bananaListVideo"
          :class="{ videoMin: isMin }"
          controls="controls"
          playsinline
          v-if="vodid == item.vodid && videoUrl"
          autoplay
          :title="item.title"
          :poster="item.coverpic"
          :picture-in-picture-mode="['push', 'pop']"
          :src="videoUrl"
        ></video>
        <!-- <img class="image" :class="{imagemMin:isMin}" v-else :src="item.coverpic" mode="aspectFit"> -->
        <img
          class="image"
          :class="{ imagemMin: isMin }"
          v-else
          v-lazy="item.coverpic"
          mode="aspectFit"
        />
        <span>{{ item.title || item.catename }}</span>
        <div class="tips">
          <span class="isvip" v-if="item.isvip == 1">{{
            item.isvip == 1 ? "vip" : ""
          }}</span>
          <span class="definition" v-if="item.definition > 0">{{
            item.definition
          }}</span>
          <span class="updatetime" v-if="!isMin">{{ item.updatetime }}</span>
          <span class="upnum">{{ item.upnum }}</span>
          <span class="scorenum">{{ item.scorenum }}</span>
          <span class="duration">{{ item.duration }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from "../js/config.js";
export default {
  props: {
    dataList: {
      default: () => {
        return [];
      },
    },
    info: {
      default: () => {
        return {};
      },
    },
    title: {
      default: "",
    },
    isMin: {
      default: false,
    },
  },
  data() {
    return {
      vodid: "",
      videoUrl: "",
    };
  },
  computed: {
    list() {
      return this.dataList.filter(
        (item) => this.$store.state.banana.isVipShow || item.isvip != 1
      );
    },
  },
  methods: {
    headerClick() {
      if (this.info.spid) {
        this.$router.push({
          path: "./detail?spid=" + this.info.spid,
        });
      } else {
        this.$router.push({
          path: "./class",
        });
      }
    },
    itemClick(item) {
      if (item.vodid != this.vodid) {
        this.$axios({
          url: config.origin + item.play_url,
          params: config.query(),
        })
          .then((res) => {
            console.log("success", res);
            if (res.data.data.httpurl) {
              this.videoUrl = res.data.data.httpurl;
              this.vodid = item.vodid;
              this.hideOtherVideo();
            } else if (res.data.data.httpurl_preview) {
              this.videoUrl = res.data.data.httpurl_preview;
              this.vodid = item.vodid;
              this.hideOtherVideo();
            } else {
              weui.topTips(res.data.errmsg || "fail");
            }
            if (document) {
              document.title = item.title;
            }
          })
          .catch((res) => {
            weui.topTips(res.message || "fail");
          });
      }
    },
    hideOtherVideo() {
      let arr = new Array(...document.querySelectorAll(".bananaListVideo"))
        .filter((item) => item.src != this.videoUrl)
        .forEach((video) => {
          video.pause();
          video.parentElement.parentElement.parentElement.__vue__.vodid = "";
          video.parentElement.parentElement.parentElement.__vue__.videoUrl = "";
        });
      console.log(arr);
    },
  },
};
</script>

<style lang="less" scoped>
.list {
  margin: 10px 0;

  .header {
    display: flex;
    justify-content: space-between;
    margin: 3px 10px;
  }

  .numInfo {
    text-align: center;
    position: sticky;
    top: 0;
    z-index: 100;
    -webkit-text-stroke: 0.3px #fff;
  }

  .items {
    display: flex;
    padding: 5px;
    flex-wrap: wrap;
    justify-content: center;

    .item {
      margin: 5px;
      margin-top: 5px;
      font-size: 14px;
      width: 320px;
      position: relative;
      box-shadow: rgb(170, 170, 170) 0px 0px 14px 0px;
      &.itemMin {
        width: 162px;
      }
      .image {
        width: 100%;
        height: 180px;
        &.imagemMin {
          height: 288px;
        }
      }

      .video {
        width: 100%;
        height: 180px;
        &.videoMin {
          height: 288px;
        }
      }

      .tips {
        padding: 5px;
        background-color: black;
        color: white;
        opacity: 0.5;
        position: absolute;
        top: 0;
        right: 0;
      }

      .definition {
        margin: 0 5px;
      }

      .isvip {
        margin: 0 5px;
      }

      .scorenum {
        margin: 0 5px;
      }

      .duration {
        margin: 0 5px;
      }
    }
  }
}
</style>
