Vue.use(Vuex);
import {
  apps
} from '../config'
const modules = require.context("./modules", true, /\.js$/).keys().reduce((modules, key) => {
  let name = key.replaceAll('./', '').replaceAll('.js', '')
  modules[name] = require(`./modules/${name}`).default
  return modules
}, {})

const store = new Vuex.Store({
  modules,
  state: {
    saveKeys: ['config', 'klineData', 'calculateData', 'accounts', 'banana'],
    config: {
      currencys: 'btc,eth',
      period: '1min,5min',
      size: '2000',
      assetsAll: 10000,
      assetsSplit: 10,
      assetsScale: 1.2,
      riseKey: 0.005,
      gas: 0.002,
    },
    klineData: [],
    calculateData: [],
    ccData: [],
    urls: {
      klineUrl: 'https://api.huobi.pro/market/history/kline',
      accounts: 'https://api.huobi.pro/v1/account/accounts',
      place: 'https://api.huobi.pro/v1/order/orders/place',
    },
    accessKey: 'ghjrgrft5g-dbcef6d9-e41da3d2-24be7',
    secretKey: '24459726-0c2a2874-4431e7f2-5f420',
    accounts: []
  },
  mutations: {
    setState(state, payload) {
      Object.keys(payload).forEach(key => {
        if (state.saveKeys.includes(key)) {
          if (typeof state[key] == 'object' && !Array.isArray(state[key])) {
            state[key] = { ...state[key], ...payload[key] }
          } else {
            state[key] = payload[key]
          }

        }
      })
    },
  },
  actions: {
  },
})
export default store;